    setNormalValue(name, v) {
        this.m_model[name] = v;
        this.m_lstNormalProperties.put(name, v);
        if (!this.m_lstDirtyProperties.contains(name))
            this.m_lstDirtyProperties.push(name);
        this.m_bDirty = true;
    }
