    /**
     * @param Develop	TRUE si se quiere que las parejas campo=valor estn separadas por AND en vez de por comas, para facilitar su uso en base de datos.
     * @return			Devuelve una cadena con las parejas campo=valor de la clave mltiple del objeto usado para identificarlo de manera nica.
     * @throws Exception
     */
    GetMultipleKeyString(Develop = false) {
        let strKey = "";
        if (this.m_owner.getMultipleKey()) {
            // Mltiples
            // Leer los valores de los campos claves y obtener una cadena que represente dicha clave
            for (let i = 0; i < this.m_owner.getKeyFields().length; i++) {
                // Obtener cada valor de campo
                let s = this.m_owner.getKeyFields()[i];
                let value = this[s];
                if (value != null) {
                    // Tiene valor
                    let strTmp = s + "=" + this.DevelopObjectValue(value);
                    if (strKey.length > 0)
                        strKey += Develop ? " AND " : ",";
                    strKey += strTmp;
                } // Tiene valor
            } // Obtener cada valor de campo
        } // Mltiples
        else
            strKey = this.getIdFieldName() + "=" + this.GetObjectIdString(Develop);
        // Completo
        return strKey;
    }
