    /**
     * Efecta el mapeo de valores enlazados una vez que se ha actualizado un campo ID mapeado a otra coleccin.
     * @param Collection		Coleccin a la cual est enlazado el campo que ha provocado el mapeo.
     * @param FieldName			Nombre del campo ID que ha provocado el mapeo.
     * @param Source			Objeto del cual se sacarn los valores que se van a copiar hacia este.
     * @return					Devuelve TRUE si todas las actualizaciones de campos son correctas.
     * @throws Exception
     */
    async DoRestoreMapValues(Collection, FieldName, Source) {
        let strFieldFrom = "", strFieldTo = "";
        let nodeList;
        let vl;
        let bUpdate = true;
        // Alejandro 02/07=2021: Correccin de error al restaurar campos mapeados
        const fieldValue = this[FieldName];
        const mapfld = this.getFieldPropertyValue(FieldName, "mapfld");
        nodeList = this.m_owner.getNodeList("prop", "linkedto", FieldName);
        for (let i = 0; i < nodeList.count(); i++) {
            // Para cada nodo
            let node = nodeList.get(i);
            if (!StringUtils.IsEmptyString(XmlUtils.getNodeAttr(node, "bit")))
                continue;
            strFieldTo = XmlUtils.getNodeAttr(node, "name");
            strFieldFrom = XmlUtils.getNodeAttr(node, "linkedfield");
            if (Source && Source instanceof XoneDataObject) {
                // Comprobar si el campo existe en el objeto
                if (!Source.FieldExists(strFieldFrom)) {
                    // Error
                    // Crear nuestro propio error
                    // M11051201: Mecanismo para soporte multilenguaje en los componentes y dems cosas.
                    // TODO ADD TAG Juan Carlos. Aado el nombre del objeto con su coleccin para que sea
                    // ms fcil reparar este error.
                    let sb = this.m_messages.GetMessage(XoneMessageKeys.SYS_MSG_OBJ_RESTOREMAPFLDFAIL_03, "{0} failed. Cannot find linked field '{1}' to restore field '{2}' in object {3}");
                    sb = sb.replace("{0}", "CXoneDataObject::DoRestoreMapValues");
                    sb = sb.replace("{1}", strFieldFrom);
                    sb = sb.replace("{2}", strFieldTo);
                    sb = sb.replace("{3}", Source.getName());
                    throw new XoneGenericException(-2444, sb);
                } // Error
                vl = Source.get(strFieldFrom);
            } // Comprobar si el campo existe en el objeto
            else if (Source && Source.isResultset) {
                // Comprobar si el campo existe en el objeto
                //int index =-1;
                try {
                    // M10052401:	Modificaciones en la forma de acceso a bases de datos SQLite.
                    // K11010501:	Modificaciones para la versin 1.5 de Android.
                    // Alejandro 02/07=2021: Parche para corregir el error al restaurar campos mapeados, siempre devolva el primer registro. Comentar con Luis la solucin final.
                    try {
                        vl = Source.getData().find((e) => e[mapfld] === fieldValue)[strFieldFrom];
                    }
                    catch (_a) {
                        vl = Source.getValue(strFieldFrom, this.m_owner.getTypeFromXml(this.m_owner.PropType(strFieldFrom)));
                    }
                }
                catch (ex) {
                    console.error(ex);
                    /*
                     * En principio la coleccin no permitir usar un resultset como origen de
                     * restauracin de campos mapeados
                     */
                    Collection.setUseObjectsInRestore(true);
                    Source.close();
                    return await this.RestoreMappedFields(FieldName);
                }
            } // Comprobar si el campo existe en el objeto
            else
                vl = null;
            if (bUpdate)
                this.put(strFieldTo, vl);
        } // Para cada nodo
        return true;
    }
    RestoreMapColValues(FieldName, mapcolValues) {
        let strFieldFrom = "", strFieldTo = "";
        let nodeList;
        let pos = NumberUtils.SafeToInt(this.GetPropertyValue(FieldName), -1);
        if (pos < 0)
            return false;
        let values = mapcolValues.split(",");
        let vl = pos >= values.length ? "" : StringUtils.SafeToString(values[pos]).trim();
        nodeList = this.m_owner.getNodeList("prop", "linkedto", FieldName);
        for (let i = 0; i < nodeList.count(); i++) {
            // Para cada nodo
            let node = nodeList.get(i);
            if (!StringUtils.IsEmptyString(XmlUtils.getNodeAttr(node, "bit")))
                continue;
            strFieldTo = XmlUtils.getNodeAttr(node, "name");
            this.put(strFieldTo, vl);
        } // Para cada nodo
        return true;
    }
