    /**
     * F11070701: Modificaciones para solucionar los enlaces y grabacin con claves no numricas.
     *
     * @return	Devuelve TRUE si el objeto es nuevo (recin creado y no se ha cargado ni grabado)
     */
    IsNew() {
        // M11080901: El mecanismo para nuevos objetos se puede flexibilizar un poco.
        // Esto podra ser diferente en funcin de si es autonumrico o no.
        let bNew = this.m_bNewObject;
        // Vale, si el objeto no es nuevo, entonces no hay nada ms que mirar
        if (!bNew)
            return bNew;
        // Si el objeto lleva clave de cadena el funcionamiento tambin es el de siempre.
        if (this.m_owner.getStringKey())
            return bNew;
        // Ahora estamos en onda numrica, puede ser manual o no.
        try {
            let vl = this.GetPropertyValue(this.m_owner.getIdFieldName());
            let l = NumberUtils.SafeToLong(vl);
            if (l <= 0)
                return bNew;
            // Si el objeto tiene valor en el campo ID en principio tenemos una moa
            // pero si el sistema es autonumrico entonces podemos considerar que el objeto no
            // es nuevo.
            let b = StringUtils.ParseBoolValue(this.m_owner.CollPropertyValue("identity-key"), true);
            // Si nos dicen que el campo de ID no es autonumrico entonces cambiarlo no cambia la condicin del objeto
            if (!b)
                return bNew;
            // Si el campo ID tiene valor y es autonumrico entonces el objeto no es nuevo.
            return false;
        }
        catch (e) {
            return bNew;
        }
    }
    isNew() {
        return this.IsNew();
    }
