    async loadFromJsonObject(dataObject, jsonObject) {
        for (let sPropertyName in jsonObject) {
            let propertyValue = jsonObject[sPropertyName];
            if (Array.isArray(propertyValue)) {
                let contentCollection = await dataObject.getContents(sPropertyName);
                if (contentCollection == null) {
                    throw new Exception("loadFromJson(): Content collection " + sPropertyName + " not found");
                }
                await this.loadFromJsonArray(contentCollection, propertyValue);
                continue;
            }
            dataObject[sPropertyName] = propertyValue;
        }
        // Iterator < String > iterator = jsonObject.keys();
        // while (iterator.hasNext()) {
        //     String sPropertyName = iterator.next();
        //     Object propertyValue = jsonObject.get(sPropertyName);
        //     if (propertyValue instanceof JSONArray) {
        //         XoneDataCollection contentCollection = dataObject.Contents(sPropertyName);
        //         if (contentCollection == null) {
        //             throw new Exception("loadFromJson(): Content collection " + sPropertyName + " not found");
        //         }
        //         loadFromJsonArray(contentCollection, (JSONArray) propertyValue);
        //         continue;
        //     }
        //     dataObject.put(sPropertyName, propertyValue);
        // }
    }
    async loadFromJsonArray(collection, jsonArray) {
        for (let i = 0; i < jsonArray.length(); i++) {
            let itemDataObject = new XoneDataObject(collection);
            await this.loadFromJsonObject(itemDataObject, jsonArray[i]);
            // let item = jsonArray[i];
            // Iterator < String > iterator = item.keys();
            // while (iterator.hasNext()) {
            //     String sPropertyName = iterator.next();
            //     Object propertyValue = item.get(sPropertyName);
            //     if (propertyValue instanceof JSONArray) {
            //         XoneDataCollection contentCollection = itemDataObject.Contents(sPropertyName);
            //         if (contentCollection == null) {
            //             throw new Exception("loadFromJson(): Content collection " + sPropertyName + " not found");
            //         }
            //         loadFromJsonArray(contentCollection, (JSONArray) propertyValue);
            //         continue;
            //     }
            //     itemDataObject.put(sPropertyName, propertyValue);
            // }
            collection.addItem(itemDataObject, i);
        }
    }
