			//     else if (!this.DataColl.Options.bUseRaw)
			//         strCmd = "SELECT * FROM (" + strAccString + ") rrss";
			//     // Comprobar si ya trae WHERE de antes, entonces no habr que cambiarlo por HAVING
			//     // porque estar bien, digo yo...
