ParserStatus.PARSER_COMPLETE = 1;
/* Estados para parsear una sentencia DELETE */
ParserStatus.PARSER_DELETE_EXPECTING_FROM = 2;
ParserStatus.PARSER_DELETE_EXPECTING_TABLE = 3;
ParserStatus.PARSER_DELETE_EXPECTING_WHERE = 4;
ParserStatus.PARSER_DELETE_WHERE = 5;
/* Estados para parsear una sentencia INSERT */
ParserStatus.PARSER_INSERT_EXPECTING_INTO = 6;
