ParserStatus.PARSER_INSERT_EXPECTING_FIELDS_BEGIN = 8;
ParserStatus.PARSER_INSERT_EXPECTING_FIELD_NAME = 9;
ParserStatus.PARSER_INSERT_EXPECTING_FIELD_SEPARATOR = 10;
ParserStatus.PARSER_INSERT_EXPECTING_VALUES = 11;
ParserStatus.PARSER_INSERT_EXPECTING_VALUES_BEGIN = 12;
ParserStatus.PARSER_INSERT_EXPECTING_VALUE = 13;
ParserStatus.PARSER_INSERT_EXPECTING_VALUE_SEPARATOR = 14;
/* Estados para parsear una sentencia UPDATE */
ParserStatus.PARSER_UPDATE_EXPECTING_TABLE = 15;
ParserStatus.PARSER_UPDATE_EXPECTING_SET = 16;
ParserStatus.PARSER_UPDATE_EXPECTING_FIELD_NAME = 17;
ParserStatus.PARSER_UPDATE_EXPECTING_EQUAL_SIGN = 18;
ParserStatus.PARSER_UPDATE_EXPECTING_FIELD_VALUE = 19;
