            //     if (m_lstFields.containsKey(strField)) {// Ahora ver si es una formulilla
            //         bStrip = true;
            //         strValueTo = GetFieldValue(strField);
            //         bFormulaFrom = IsFormula(strValue);
            //         bFormulaTo = IsFormula(strValueTo);
