import { sprintf as pr } from "sprintf-js";
String.prototype.equals = function (text) {
    return this.localeCompare(text) === 0;
};
String.prototype.equalsIgnoreCase = function (text) {
    return this.compareToIgnoreCase(text) === 0;
};
String.prototype.contains = function (text) {
    return this.indexOf(text) >= 0;
};
String.prototype.compareToIgnoreCase = function (text) {
    return this.toLowerCase().localeCompare(text.toLocaleLowerCase());
};
String.prototype.compareTo = function (text) {
    return this.localeCompare(text);
};
String.format = function (text, ...args) {
    return pr(text, ...args);
    //return ft(text,...args);
};
