    constructor(appData, source, version) {
        this.UNIQUE_ID = Symbol("XoneDataCollection");
        this.m_ownerApp = appData;
        this.m_xmlNode = source; // new XoneDataSource(null,source);
        this.m_version = version;
        this.m_messages = appData.getMessageHolder();
        this.m_options = {};
        this.m_strPk = "ID";
        this.m_nPkType = 0;
        this.m_parent = null;
        this.m_bLocked = false;
        this.m_macrosEvaluator = new MacrosEvaluator(this.m_ownerApp, this);
        // this.m_lstMacros = new Hashtable<string, string>();
        // Si la coleccin no tiene nombre nos explotamos...
        // M11051201: Mecanismo para soporte multilenguaje en los componentes y dems cosas.
        if (StringUtils.IsEmptyString((this.m_strName = this.m_xmlNode.getAttrValue("name"))))
            ////throw new Exception("No se puede crear una coleccin sin nombre o con nombre vaco.");
            throw new Exception(this.m_messages.GetMessage(XoneMessageKeys.SYS_MSG_COLL_NONAME, "Cannot create a collection with an empty name."));
        // Crear la lista de claves mltiples por si las moscas
        this.m_lstKeyFields = new Vector();
        // O12050702: Optimizacin al gestionas los campos que son de frmula o bit.
        this.m_lstFormulaProps = new Array();
        this.m_lstBitProps = new Array();
        this.m_cachePlatformNodes = new Hashtable();
        // Iniciar el browserdata
        this.m_browserData = new XoneBrowseData(this);
    }
    // Solo para identificar que una instancia es una coleccin
    get isDataCollection() {
        return true;
    }
