            strClassName = this.Options.strClassName = this.m_ownerApp.GetClassName(this.Options.strProgId);
        else
            strClassName = this.m_ownerApp.CheckClassName(this.Options.strClassName);
        // A12122701: Sistema para gestionar la estructura lgica de vistas en la maquinaria.
        // Si el layout no est construido del todo entonces tendremos que esperar
        // TODO ADD TAG
        // if (m_layout !=null)
