    PropertyTitle(FieldName) {
        let strProp = "", str = "";
        let node = null, platNode = null;
        if (!StringUtils.IsEmptyString((strProp = this.FieldPropertyValue(FieldName, "title"))))
            return strProp; // Este mismo y pa la pinga
        // M10090801:	Modificaciones para permitir que los <button> sean considerados propiedades.
        // El atributo caption tambin vale (en el caso de los botones, sentiende)
        if (!StringUtils.IsEmptyString((strProp = this.FieldPropertyValue(FieldName, "caption"))))
            return strProp;
        //
        // Buscarlo sin ms...
        let coll = this;
        do {
            if (null != (node = coll.getNode("prop", "name", FieldName)))
                break;
            // M10090801:	Modificaciones para permitir que los <button> sean considerados propiedades.
            // Comprobar si se trata de un botoncejo antiguo
            if (null != (node = coll.getNode("button", "name", FieldName)))
                break;
            coll = coll.getParentCollection();
        } while (coll != null);
        if (node == null)
            return FieldName;
        // Cambiar el chequeo pues el puntero puede ser nulo. No se soportan
        // cadenas vacas.
        if (!StringUtils.IsEmptyString((str = this.getOwnerApp().getPlatform()))) {
            // Buscar el nodo
            platNode = node.SelectSingleNode("platform", "name", str);
        } // Buscar el nodo
        str = "";
        // M10090801:	Modificaciones para permitir que los <button> sean considerados propiedades.
        // En caso de los botones, vale caption tambin
        if (platNode != null) {
            // Comprobar title y caption
            if (StringUtils.IsEmptyString((str = XmlUtils.getNodeAttr(platNode, "title"))))
                str = platNode.getAttrValue("caption");
        } // Comprobar title y caption
        if (StringUtils.IsEmptyString(str)) {
            // No est en el nodo de plataforma
            str = XmlUtils.getNodeAttr(node, "title");
            if (StringUtils.IsEmptyString(str)) {
                // Si no tiene title, ver si tiene texto
                // M10090801:	Modificaciones para permitir que los <button> sean considerados propiedades.
                // Comprobar tambin caption, que para los botones vale lo mismo
                str = node.getAttrValue("caption");
                if (StringUtils.IsEmptyString(str))
                    str = node.getText();
                if (StringUtils.IsEmptyString(str)) {
                    // Si es un botn, no devolver el nombre
                    // F10100708:	Los botones que no tengan texto deberan devolver ttulo vaco.
                    // Cambiar este mecanismo porque puede explotarse si no hay Type.
                    let bButton = false;
                    if (node.getName().equals("button"))
                        bButton = true;
                    else {
                        // Obtener el tipo
                        let strType = node.getAttrValue("type");
                        if (!StringUtils.IsEmptyString(strType)) {
                            // Ver si es un botn
                            if (strType.equals("B"))
                                bButton = true;
                        } // Ver si es un botn
                    } // Obtener el tipo
                    if (!bButton)
                        str = FieldName;
                } // Si es un botn, no devolver el nombre
            } // Si no tiene title, ver si tiene texto
        } // No est en el nodo de plataforma
        return StringUtils.SafeToString(str);
    }
