    FieldExists(FieldName) {
        // Buscar si el campo existe en la lista del nodo XML
        if (null != this.m_xmlNode.SelectSingleNode("prop", "name", FieldName))
            return true;
        // M10090801:	Modificaciones para permitir que los <button> sean considerados propiedades.
        // Lo mismo con los botones
        if (null != this.m_xmlNode.SelectSingleNode("button", "name", FieldName))
            return true;
        // F10052502:	FieldExists no debe comprobar existencia del campo clave.
        if (this.m_strPk.equals(FieldName))
            return true;
        // Buscar si heredamos de alguna, en ese caso buscar en el parent
        if (!StringUtils.IsEmptyString(this.Options.strParentCollection)) {
            // Comprobar
            if (this.getParentCollection() == null) {
                // Error
                // M11051201: Mecanismo para soporte multilenguaje en los componentes y dems cosas.
                ////throw new XoneGenericException(-17778, "CXoneDataCollection::FieldExists ha fallado. La coleccin '" + m_strName + "' hereda de '" + m_strParentCollection + "' y no se encuentra la coleccin padre.");
                let sb = this.m_messages.GetMessage(XoneMessageKeys.SYS_MSG_COLL_FIELDEXISTSFAIL, "CXoneDataCollection::FieldExists failed. Collection '{0}' inherits '{1}' and it cannot be found.");
                sb = sb.replace("{0}", this.m_strName);
                sb = sb.replace("{1}", this.Options.strParentCollection);
                throw new XoneGenericException(-17778, sb);
            } // Error
            return this.getParentCollection().FieldExists(FieldName);
        } // Comprobar
        // De lo contrario.. nanai...
        return false;
    }
