    /**
     * Limpia la coleccin de los objetos que hay en memoria. Si la coleccin est bloqueada no hace nada.
     * @return			TRUE si la operacin es correcta.
     */
    clear() {
        // Si est bloqueada, no hacemos nada...
        if (this.m_bLocked)
            return true;
        return this.ClearCollection();
    }
    /**
     * Efecta la limpieza real de la coleccin aunque esta est bloqueada
     * @return		Devuelve TRUE si la coleccin se limpia correctamente.
     */
    ClearCollection() {
        if (this.m_bIsClearing || this.m_bLocked)
            return true; // Ya est dentro...
        try {
            this.m_bIsClearing = true;
            // Ahora limpiar las colecciones porsi...
            this.BrowseData.Clear();
            this.m_bFull = this.m_bIsClearing = false;
            // Si es especial esto es como un EB
            if (this.m_bIsSpecial)
                this.m_nBrowseLength = -1;
        }
        catch (e) {
            // Ignorar las excepciones a menos que sea debug
            if (this.Options.bDebug) {
                // Imprimir
                //this.m_ownerApp.WriteConsoleString(e.getMessage ());
                console.log(e.message);
            } // Imprimir
        }
        return true;
    }
