    async ExecuteCollAction(ActionNode, ActionName, ...Arguments) {
        return await this.executeCollAction(ActionNode, ActionName, ...Arguments);
    }
    async executeCollAction(ActionNode, ActionName, ...Arguments) {
        let node = null;
        let str = "";
        if (StringUtils.IsEmptyString(ActionNode))
            return null;
        // Primero tenemos que buscar el nodo correspondiente al prefijo
        let prefix = this.GetNode(ActionNode);
        if (prefix == null) {
            // No existe el nodo
            if (this.Options.bDebug)
                console.log("Coleccin '" + this.getName() + "' ExecuteCollAction no encuentra el nodo '" + ActionNode + "'");
            // Esto en s no es un fallo de ejecucin
            return null;
        } // No existe el nodo
        if (!StringUtils.IsEmptyString(ActionName))
            node = prefix.SelectSingleNode("action", "name", ActionName);
        else
            node = prefix.SelectSingleNode("action");
        if (null != node) {
            // Comprobar qu tipo de accin es
            str = XmlUtils.GetNodeAttr(node, "type");
            // F11051805: Permitir que los nodos coll-action no tengan tipo.
            // Por defecto...
            if (StringUtils.IsEmptyString(str))
                str = "runscript";
            // K13080701: La ejecucin de scripts con argumentos a nivel de coleccin falta en algunos casos.
            // Pasar los argumentos que nos han mandado (puede ser nulo)
            if (str.equals("runscript"))
                return await this.doRunScript(node, Arguments);
        } // Comprobar qu tipo de accin es
        else {
            // Si est en debug, dejar traza
            if (this.Options.bDebug) {
                // Mostrar mensaje
                if (StringUtils.IsEmptyString((str = ActionName)))
                    str = "";
                console.log("Coleccin '" + this.getName() + "' ExecuteCollAction no encuentra el nodo action[name='" + str + "']");
            } // Mostrar mensaje
        } // Si est en debug, dejar traza
        // No hacer nada de nada...
        return null;
    }
